<template>
  <div>
    <b-container class="pt-4">
      <div class="mb-5 large-title mt-5">Список задач</div>

      <div class="mb-3">
        <input type="text" class="serchInput" placeholder="Введите название регламента">
      </div >

      <b-form-group mt-5>
        <b-form-checkbox-group id="checkbox-group-2" class="md-text" v-model="filterParams" name="flavour-2">
          <b-form-checkbox value="orange">В работе</b-form-checkbox>
          <b-form-checkbox class="ml-2" value="apple">Согласован</b-form-checkbox>
          <b-form-checkbox class="ml-2" value="pineapple">На согласовании</b-form-checkbox>
          <b-form-checkbox class="ml-2" value="grape">Ошибка</b-form-checkbox>
        </b-form-checkbox-group>
      </b-form-group>
    </b-container>



    <div class="md-text mt-5">
      <b-container >
        <b-row
        v-for="reglament in reglaments"
        :key="reglament.id"
          @click="$router.push(`/task/${reglament.id}`)"
          class="task-item w-100 cursor-pointer"
          align-v="center"
        >
          <b-col cols="8" class="justify-content-between d-flex align-items-center">
            <div class=" d-flex align-items-center">
              <div class="dot-wrapper mr-3 pl-1 pt-1">
                <div class="mt-2"><b-icon class="float-right icon-xl text-blue" icon="file-earmark"></b-icon></div>
              </div>
              <div class="ml-3">
                <div>{{reglament.name}}</div>
                <div class="small_grey-text">
                  <span>PDF 7,8 МБ</span>
                  <span class="ml-2 ">{{moment(reglament.updated_at).format('DD.MM.YYYY HH:mm')}}</span>
                </div>
              </div>
            </div>
          </b-col>
          <b-col cols="4"
            class="text-right "
            :class="{
              'text-yellow' : reglament.status === 'Согласование' || reglament.status === 'На согласовании',
              'text-blue' : reglament.status === 'Согласован',
              'text-red' : reglament.status === 'Ошибка',
              'text-black' : reglament.status === 'В работе',
            }"
          >
            {{reglament.status}}
          </b-col>
        </b-row>
      </b-container>
    </div>

  </div>
</template>

<script>
import mainService from '../sevices/mainService'
import moment from 'moment'
// import { mapActions, mapGetters } from 'vuex'

export default {
  name: 'Tasks',
  components: {
  },
  data: () => ({
    filterParams: [],
    reglaments: [],
    moment
  }),
  created() {
    this.getReglaments();
  },
  computed: {
    // ...mapGetters([
    //   'currentUser',
    //   'loginFormStatus'
    // ])
  },
  methods: {
    getReglaments() {
      mainService.getReglaments()
        .then(({data}) => {
          console.log(data.results)
          this.reglaments = data.results;
        })
    }
  }
}
</script>

<style lang="scss" scoped>
  .task-item{
    height: 68px;
    border-bottom: 1px solid #D9D9DE;
  }
  .serchInput{
    width: 576px;
    border: 1px solid #0061D9;;
  }
</style>
